import { createRouter, createWebHistory } from 'vue-router'
const routes = [
  {
    path : '/login',
    name : 'LoginAndRegister',
    component : () => import('@/views/LoginPage.vue')
  },
  {
    path : '/home',
    name : 'Home',
    component : () => import('@/views/MainComponent.vue')
  },
  {
    path : '/acount/create',
    name : 'CreateAccount',
    component : () => import('@/views/CreateAccountPage.vue')
  },
  {
    path: '/:catchAll(.*)',
    name: 'CatchAll',
    component: () => import('@/components/status-http-server/404NotFound.vue')
  },{
    path : '/',
    name : 'Home',
    component : () => import('@/views/LoginPage.vue')
  },
  {
    path : '/admin/user-manager',
    name : 'UserManager',
    component : () => import('@/views/admin/table/UserManager.vue')
  },
  {
    path : '/admin',
    name : 'Admin',
    component : () => import('@/views/MainComponent.vue')
  },
  {
    path : '/admin/org-manager',
    name : 'OrgManager',
    component : () => import('@/views/admin/table/OrganzinationManager.vue')
  },
  {
    path : '/admin/role-manager',
    name : 'RoleManager',
    component : () => import('@/views/admin/table/RoleManager.vue')
  },{
    path : '/admin/resource-manager',
    name : 'ResourceManager',
    component : () => import('@/views/admin/table/ResourceManager.vue')
  },{
    path : '/admin/permission-manager',
    name : 'PermissionManager',
    component : () => import('@/views/admin/table/PermissionManager.vue')
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes : routes
})
router.beforeEach((to, from, next) => {
  const publicPages = ['/login', '/acount/create','/admin/dashboard'];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = localStorage.getItem('token');
  if (authRequired && !loggedIn) {
    return next('/login');
  }
  next();
});

export default router;
